.footer {
	padding-top: 40px;
	padding-bottom: 30px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: auto;
}

.footer__number {
	@include number;
	color: $color-black;
	user-select: none;
	@include numberStates;
}

.footer__logo {
	@include footerLogoStates;
}